import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  TextField,
  FormControl,
  Button,
  Checkbox,
  Container,
  Grid,
  FormControlLabel,
  Typography,
} from "@mui/material";
import { useSnackbar } from "notistack";
import axios from "axios";
import { makeStyles } from "@mui/styles";

const styles = makeStyles({
  register_container: {
    marginTop: "50px",
    marginBottom: "50px",
    textAlign: "center",
    border: "0 solid #DBD7D7",
    borderTopWidth: "0.1px",
    borderLeftWidth: "0.1px",
    borderRadius: "10px 10px 10px 10px",
    WebkitBoxShadow: "27px 43px 43px -20px rgba(89,89,89,0.39)",
  },
  register_title: {
    marginTop: "25px",
    paddingBottom: "20px",
    fontWeight: "bolder",
    fontSize: "27px",
    borderBottom: "1px solid #DBD7D7",
  },
});

function Register() {
  const [Inputs, setInputs] = useState({
    id: "",
    password: "",
    repassword: "",
    name: "",
    phone: "",
    checked: false,
    checked1: false,
    checked2: false,
  });

  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  function Alerts(contents, color) {
    return enqueueSnackbar(contents, {
      variant: color,
    });
  }

  const { id, password, repassword, name, phone, checked, checked1, checked2 } =
    Inputs;

  const classes = styles();

  function onChangeInput(e) {
    const { name, value } = e.target;
    setInputs({
      ...Inputs,
      [name]: value,
    });
  }
  function onChangeCheck(e) {
    const { checked, name } = e.target;
    setInputs({
      ...Inputs,
      [name]: checked,
    });
  }

  async function onClick(e) {
    e.preventDefault();

    var regPhone = /^01([0|1|6|7|8|9])?([0-9]{3,4})?([0-9]{4})$/;

    if (id === "") {
      return Alerts(" .", "info");
    } else if (password === "") {
      return Alerts(" .", "info");
    }
    if (password !== repassword) {
      return Alerts(" ", "info");
    } else if (password.length > 8) {
      return Alerts(" 8 20 .", "info");
    } else if (name === "") {
      return Alerts(" .", "info");
    } else if (phone === "") {
      return Alerts(" .", "info");
    } else if (!regPhone.test(phone)) {
      return Alerts("  .", "info");
    }

    let data = {
      name: name,
      phone: phone,
      id: id,
      password: password,
      checked: checked2,
    };

    const res = await axios.post("http://localhost:8080/register", data);
    if (res.data === "ID") {
      return Alerts("   .", "info");
    } else if (res.data) {
      Alerts(" .", "info");
      return navigate("/login", { replace: true });
    } else {
      return Alerts(
        "  . .",
        "info"
      );
    }
  }

  const value = [
    { label: "", name: "id" },
    { label: "", name: "password" },
    { label: " ", name: "repassword" },
    { label: "", name: "name" },
    { label: " (-) ", name: "phone" },
  ];

  return (
    <>
      <Container maxWidth="lg">
        <Container maxWidth="md" className={classes.register_container}>
          <Typography className={classes.register_title}></Typography>
          <Grid>
            <FormControl>
              {value.map((v, i) => {
                return (
                  <TextField
                    key={i}
                    label={v.label}
                    name={v.name}
                    type={
                      v.name === "password" || v.name === "repassword"
                        ? "password"
                        : "text"
                    }
                    placeholder={
                      v.name === "password"
                        ? "8 20 ."
                        : ""
                    }
                    margin="normal"
                    required
                    autoFocus
                    variant="standard"
                    onChange={onChangeInput}
                  />
                );
              })}
              <FormControlLabel
                control={<Checkbox name="checked" onChange={onChangeCheck} />}
                label=" ?"
                sx={{ mt: 1 }}
              />
              <FormControlLabel
                control={<Checkbox name="checked1" onChange={onChangeCheck} />}
                label="()    
               "
              />
              <FormControlLabel
                control={<Checkbox name="checked2" onChange={onChangeCheck} />}
                label="()   SNS    "
              />
              <Button
                type="submit"
                variant="outlined"
                disabled={!checked || !checked1 ? true : false}
                sx={{ mt: 3, mb: 5, p: 2 }}
                onClick={onClick}
              >
                
              </Button>
            </FormControl>
          </Grid>
        </Container>
      </Container>
    </>
  );
}

export default Register;
